<template>
  <div class = "Bulletin">
    <div class = "crumbs">
      <el-breadcrumb separator = "/">
        <el-breadcrumb-item><i class = "el-icon-lx-calendar"></i>公告栏文本编辑</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class = "container">
      <div class = "Bulletin_text">
        <h1>公告栏文本</h1>
        <span>{{text}}</span>
      </div>
      <!--修改文本-->
      <div class = "Bulletin_text">
        <h1>修改公告栏文本</h1>
        <textarea placeholder = "请输入修改公告栏文本" v-model = "txt"></textarea>
      </div>
      <el-button type = "primary" @click = "addtext">修改提交</el-button>
    </div>
  </div>
</template>
<script>
  export default {
    name: "Bulletin",
    data() {
      return {
        text: '', // 公告栏文本查询
        txt: '', // 修改后的公告栏文本
      }
    },
    created() {
      this.chanesetext()
    },
    methods: {
      //查询公告栏文本
      chanesetext() {
        this.$axios.post('/api/admin/daohang', {}, {}).then(res => {
          this.text = res.data.data
          console.log(res);
        }).catch(err => {
          console.log(err);
        })
      },
      //  修改公告栏文本
      addtext() {
        let params = {
          data: this.txt
        }
        this.$axios.post('/api/admin/daohang/daohangchange', this.$qs.stringify(params), {}).then(res => {
          this.chanesetext()
          this.$message.success(`修改公告栏成功`);
          this.txt = ''
          console.log(res);
        }).catch(err => {
          console.log(err);
        })
      }
    }
  }
</script>
<style scoped lang = "scss">
  .Bulletin_text {
    width: 50%;
    height: 200px;
    overflow: scroll;
    span {

    }
    textarea {
      width: 100%;
      height: 150px;
    }
  }
</style>



